@import '../../Sass/GlobalFunctions.module.scss';
@import '../../Sass/GlobalMixins.module.scss';

#Projects {
    color: #fff;
    align-items: center;
    display: flex;
    line-height: 18pt;
    flex-direction: column;
    justify-content: flex-start;
    min-height: 100vh;
    width: 100%;

    #ProjectsContentContainer {
        display: flex;
        width: 60%;
        justify-content: center;
        h1 {
            font-size: 2.5em;
            margin-top: 5%;
            text-align: center;
            width: auto;
            padding: 20px 0;
            border-bottom: solid black 2px;
            font-weight: 800;
        }
        min-height: 100%;
    }

    .NavContainer {
        width: 110%;
        padding-left: -5%;
        margin-bottom: 2%;
    }
}

@include mobile {
    .NavContainer {
        width: 100%;
    }
}